import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { AlertSecurityComponent } from './alert-security.component';
import { SendPanicAlertComponent } from './components/send-panic-alert/send-panic-alert.component';
import { SecurityMyHistoryComponent } from './components/security-my-history/security-my-history.component';
import { SecurityEmergencyContactNumbersComponent } from './components/security-emergency-contact-numbers/security-emergency-contact-numbers.component';


const routes: Routes = [
	{ path: '', redirectTo: 'send-panic-alert', pathMatch: 'full'},
	{ path: 'send-panic-alert', component: SendPanicAlertComponent},
	{ path: 'my-history', component: SecurityMyHistoryComponent},
	{ path: 'emergency-contacts', component: SecurityEmergencyContactNumbersComponent},
	{ path: '**', redirectTo: 'send-panic-alert', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AlertSecurityRoutingModule { }
