import { Component, OnInit } from '@angular/core';

import { AlertService } from '../../../.././api/services/alert.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-security-emergency-contact-numbers',
  templateUrl: './security-emergency-contact-numbers.component.html',
  styleUrls: ['./security-emergency-contact-numbers.component.scss']
})
export class SecurityEmergencyContactNumbersComponent implements OnInit {
  
  user:any;

  emergencyList:any;

  isContactsLoaded:boolean = false;

  constructor(
  	private alertService:AlertService,
    private sharedService:SharedService,
    private lookupService: LookupService,
    private cookieService:CookieService
  ) { }

  ngOnInit() {


  	this.alertService.getAllAlertEmergencyNumbers().subscribe((res:any) => {

  	  this.isContactsLoaded = true;

      this.emergencyList = res;


    },
    error => {
      console.log(error);
    });

  }

}
