import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import {Router} from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { UserService } from '../../../.././api/services/user.service';
import { ApartmentService } from '../../../.././api/services/apartment.service';
import { AlertService } from '../../../.././api/services/alert.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

@Component({
  selector: 'app-send-panic-alert',
  templateUrl: './send-panic-alert.component.html',
  styleUrls: ['./send-panic-alert.component.scss']
})
export class SendPanicAlertComponent implements OnInit {
   
  emergencyList:any;

  modalService:any;

  userName:any;

  isEmergenciesLoaded:boolean = false;

  rangeValue:any = 0;

  maxRange:any = 10;

  apartmentBlockUnitId:any;
  alertId:any;

  confirmDialogRef:any;

  isRangeDone:boolean = false;
  isRequestSuccess:boolean = false;
  isRequestDeleted:boolean = false;

  gpsLocation:any;

  @ViewChild("confirmEmergencyRef", { static: false }) confirmEmergencyRef: TemplateRef<any>;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private router: Router,
    private alertService:AlertService,
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private sharedService:SharedService,
    private lookupService: LookupService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }


  getEmergencyType(id){

    //medical
    if(id == 130){
      return 'medical';
    }
    else if(id == 131){
      return 'theft';
    }
    else if(id == 132){
      return 'fire';
    }
    else if(id == 133){
      return 'shooter';
    }
    else if(id == 134){
      return 'police';
    }
    else {
      return 'other';
    }
  }

  getRangeValue(alert){
    if(this.rangeValue == '100') {
      
      this.isRangeDone = true;


      let details = {

        "apartmentBlockUnitId": this.apartmentBlockUnitId,
        "receivedDate": "2019-12-06T13:45:09.296Z",
        "alertTypeId": alert.lookupValueId,
        "location": "string",
        "gpslocation": this.gpsLocation,
        "assignedTo": 0,
        "alertStatusId": 135, // id for "OPEN" status
        "notes": "",
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-12-06T13:45:09.296Z",
        "updatedBy": 0,
        "updatedOn": "2019-12-06T13:45:09.296Z"

      }

      this.alertService.addApartmentBlockUnitAlert(details).subscribe((res:any) => {

        if(res.message) {
          this.isRangeDone = false;  
          this.isRequestSuccess = true;
          this.alertId = res.message;
        }
        


      },
      error => {
        console.log(error);
      });


    }
  }

  closeAlertModal(){

    this.confirmDialogRef.close();

    this.confirmDialogRef.afterClosed().subscribe(
      data => {
         this.router.navigateByUrl('user/alert-security/my-history');
      });


  }

  showConfirmEmergency(item, type){

    var alert = item;
    alert.type = type;


    this.confirmDialogRef = this.dialog.open(this.confirmEmergencyRef, {
        panelClass: 'material',
        disableClose: true,
        data: alert
    });

    this.confirmDialogRef.afterClosed().subscribe(
      data => {
        this.isRangeDone = false;
      }
    );

  }

  cancelEmergency() {

    this.isRangeDone = true;

    var params = {
      apartmentBlockUnitAlertId: this.alertId,
      deleteBy: parseInt(this.cookieService.get('userId'))
    }

    this.alertService.deleteApartmentBlockUnitAlert(params).subscribe((res:any) => {

        if(res.message) {
          this.isRangeDone = false;  
          this.isRequestDeleted = true;
        }
        


    },
    error => {
      console.log(error);
    });

  }

  setCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.gpsLocation = position.coords.latitude+','+position.coords.longitude;
      });

    }
  }

  ngOnInit() {

    //get emergency services
    this.lookupService.getLookupValueByLookupTypeId(29).subscribe((res : any) => {

      this.isEmergenciesLoaded = true;

      this.emergencyList = res;

    });

    this.userService.getUserById(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

      this.userName = res[0].firstName;

      this.apartmentService.getApartmentBlockUnitByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

        this.apartmentBlockUnitId = res[0].apartmentBlockUnitId;

      },
      error => {
        console.log(error);
      }
      );


    },
    error => {
      console.log(error);
    });

    this.setCurrentLocation();

  }

}
