import { Component, OnInit } from '@angular/core';

import { ApartmentService } from '../../../.././api/services/apartment.service';
import { AlertService } from '../../../.././api/services/alert.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import * as moment from 'moment';

@Component({
  selector: 'app-security-my-history',
  templateUrl: './security-my-history.component.html',
  styleUrls: ['./security-my-history.component.scss']
})
export class SecurityMyHistoryComponent implements OnInit {

  alertList:any;

  isAlertsLoaded:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  apartmentBlockUnitId:any;

  emergencyTypeList:any;
  alertTypeList:any;

  constructor(
    private apartmentService:ApartmentService,
  	private alertService:AlertService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getEmergencyType(id){

    //medical
    if(id == 130){
      return 'medical';
    }
    else if(id == 131){
      return 'theft';
    }
    else if(id == 132){
      return 'fire';
    }
    else if(id == 133){
      return 'shooter';
    }
    else if(id == 134){
      return 'police';
    }
    else {
      return 'other';
    }
  }

  getAlertName(id) {
    
    var data = _.filter( this.emergencyTypeList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getAlertStatus(id) {
    
    var data = _.filter( this.alertTypeList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getDate(date) {
    return moment(date).format("MM-DD-YYYY, h:mm A");
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  ngOnInit() {


    this.apartmentService.getApartmentBlockUnitByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

        this.apartmentBlockUnitId = res[0].apartmentBlockUnitId;

        this.alertService.getAllApartmentBlockUnitAlertByApartmentBlockUnitId(this.apartmentBlockUnitId).subscribe((alert:any) => {

          this.alertList = alert.filter(item => {
            return item.isActive;
          });

          this.totalItems = this.alertList.length;

          if(this.totalItems>this.itemLimit){
            this.ItemEndIndex = this.itemLimit;
          }
          else {
            this.ItemEndIndex = this.totalItems;
          }

          this.isAlertsLoaded = true;

          console.log(this.alertList);

        },
        error => {
          console.log(error);
        }
        );


    },
    error => {
      console.log(error);
    }
    );

    //get emergency types
    this.lookupService.getLookupValueByLookupTypeId(29).subscribe((res : any) => {

      this.emergencyTypeList = res;

    });

    //get alert status types
    this.lookupService.getLookupValueByLookupTypeId(30).subscribe((res : any) => {

      this.alertTypeList = res;

    });

  }

}
